.container {
    display: flex;
    flex-direction: row;
}

.column {
    flex: 1;
    padding: 20px;
}

.image-container {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;
}

.image-container img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    object-fit: cover;
    height: 1189px;
}

.text {
    font-family: 'Taipei Sans TC Beta', sans-serif;
    font-size: 36px;
}

.title {
    position: absolute;
    top: 30%;
    left: 50%;
    font-weight: bold;
    color: #000;
}

.date {
    font-family: 'Taipei Sans TC Beta', sans-serif;
    position: absolute;
    color: #000;
    top: 410px;
    left: 58%;
    font-weight: bold;
    font-size: 48px;
}

.speaker_name {
    position: absolute;
    top: 780px;
    left: 49%;
    font-family: 'Arial', sans-serif;
    font-size: 48px;
    color: white;
    font-style: italic;
    font-weight: 800;
}

.career {
    position: absolute;
    top: 780px;
    left: 70%;
    color: white;
}

.topic {
    position: absolute;
    top: 100%;
    font-size: 24px;
    top: 900px;
    padding: 1%;
}